.arrow-up {
    z-index: 99;
    @include flexCenter;
    text-align: center;
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    width: 3rem;
    height: 3rem;
    background-color: rgba(0,0,0,.7);
    @include mobile-version {
        bottom: .5rem;
        right: .5rem;
    }
    div {
        color: white;
        &::before {
            @include icon('\f077', 2.4rem);
        }
    }
}

.dark .switch-button {
    border-color: white;
    &::before {
        transform: translateX(1.3em);
        background-color: #fff;
    }
}

.back {
    z-index: 99;
    position: absolute;
    width: 30px;
    height: 30px;
    top: .5em;
    left: .5em;
    &::before {
        @include icon('\f3e5',2em);
        @include tablette-version {
            font-size: 6vw;
        }
    }
    &.hide {
        display: none;
    }
}

button {
    background-color: black;
    color: #fff;
    border: solid;
    border-radius: 1em;
    padding: .5em 1em;
    font-size: 1em;
}