.menu-container {
    width: 60%;
    height: 50%;
    background-color: black;
    border: solid;
    border-radius: 1em;
    nav {
        width: 100%;
        height: 100%;
        @include flexCenter;
    }
    ul {
        width: 60%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        li {
            font-size: 3em;
        }
    }
    .connect-page {
        position: absolute;
        top: 0.5em;
        right: 0.5em;
        font-size: .6em;
    }
}