.disco-main {
    position: relative;
    width: 95%;
    height: 90%;
    background-color: black;
    border: solid;
    border-radius: 1em;
    @include flexColumn;
    overflow: hidden;
    h1 {
        margin: 1em 0;
        font-size: 2em;
    }
    input {
        color: black;
    }
    .disc-full {
        z-index: 99;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: black;
        @include flexCenter;
    }
    .close-disc {
        position: absolute;
        @include flexCenter;
        top: 2em;
        right: 2em;
        color: white;
        border: solid .2em;
        border-radius: 50%;
        width: 2em;
        height: 2em;
        font-weight: bold;
    }
    .cover {
        width: 25em;
        height: 25em;
        position: relative;
        transition: 500ms;
        transform-style: preserve-3d;
        &.flip {
            transform: rotateY(180deg);
        }
    }
    .cover-back,.cover-front {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
    }
    .cover-front {
        img {
            @include imageFit;
        }
    }
    .cover-back {
        transform: rotateY(180deg);
        border: solid .2em;
        padding: 1em;
        p {
            font-size: 2em;
            margin-bottom: .5em;
        }
    }
    .filter-nav {
        display: flex;
        font-size: 1.5em;
        margin-top: .5em;
        select {
            color: black;
        }
        .filter-field {
            margin-right: 1em;
        }
        label {
            margin-right: .3em;
        }
    }
    .new-disc {
        position: absolute;
        top: .5em;
        right: .5em;
        font-size: 1.5em;
    }
    .discs-container {
        width: 95%;
        height: 68%;
        @include flexColumn;
        
        position: relative;
    }
    tbody,tr {
        width: 99%;
    }
    thead {
        position: fixed;
        width: 87vw;
        margin-right: 1em;
    }
    tbody {
        margin-top: 2em;
        overflow: auto;
    }
    tr {
        display: flex;
        align-items: center;
        position: relative;
    }
    th {
        font-size: 1.1em;
        @include flexCenter;
    }
    .selected {
        margin-left: .5em;
        &.hide {
            display: none;
        }
    }
    .selected.up::before {
        @include icon('\f0de');
    }
    .selected.down::before {
        @include icon('\f0dd');
    }

    td {
        @include flexCenter;
        margin: .2em 0;
        font-size: 1.2em;
        overflow: hidden;
        p {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .disc-actions {
        display: flex;
        position: absolute;
        left: 0;
    }
    .modify-disc {
        position: relative;
        display: flex;
        align-items: center;
        &::before {
            @include icon('\f303');
        }
        &:hover .label {
            opacity: 1;
            z-index: 50;
        }
    }
    .show-disc {
        cursor: pointer;
        display: flex;
        align-items: center;
        position: relative;
        margin-right: 1em;
        &::before {
            @include icon('\f51f');
        }
        &:hover .label {
            opacity: 1;
            z-index: 50;
        }
    }
    .label {
        z-index: -1;
        position: absolute;
        background-color: white;
        color: black;
        right: -4em;
        font-size: .6em;
        opacity: 0;
        transition: opacity .8s;
        padding: .2em;
        border-radius: .2em;
    }
    .light-row {
        background-color: rgb(41, 41, 41);
    }
    .collector {
        position: absolute;
        font-size: 2em;
        right: 0;
    }
    .footer-marge {
        width: 100%;
        display: flex;
        align-items: center;
        padding: .5em 0 .5em 1em;
    }
    .album-column,.artist-column {
        width: 26vw;
    }
    .artist-column {
        margin: 0 1em 0 3.5em;
    }
    .year-column {
        width: 8vw;
    }
    .genre-column {
        width: 10vw;
    }
    .format-column {
        width: 11vw;
    }
}