*, ::before, ::after {
  margin: 0;
  padding: 0;
  color: white;
  box-sizing: border-box;
}


body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  min-height: 100vh;
  background-color: black;
  background: url(../img/discs_960.webp) fixed no-repeat;
  @media (orientation: portrait) {
    background: url(../img/discs_mini.webp) fixed no-repeat;
  }
	background-size: 100%;
}

#root {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.App {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  transition: .8s;
  width: 100vw;
  height: 100vh;
  @include flexCenter;
  font-size: 1.5vw;
}

ul, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.dark {
  background-color: black;
  *,::before,::after {
    color: #fff;
  }
}

.previous-page {
  cursor: pointer;
  z-index: 50;
  position: fixed;
  bottom: 1em;
  left: 1em;
  border: none;
  background-color: transparent;
  box-shadow: none;
  &::before {
      @include icon('\f137',2em);
  }
}

.alert-window {
  position: absolute;
  @include flexColumn;
  justify-content: center;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: black;
}

.alert-message-container {
  border: solid white;
  border-radius: 2em;
  width: 80%;
  height: 60%;
  @include flexCenter;
  padding: 2em;
  p {
    font-size: 3em;
  }
}